@property --gradientDeg {
	syntax: '<angle>';
	inherits: false;
	initial-value: 0deg;
}

@keyframes gradientRotate {
	0% {
		--gradientDeg: 0deg;
		filter: blur(30px);
	}
	50% {
		filter: blur(20px);
	}
	100% {
		--gradientDeg: 360deg;
		filter: blur(30px);
	}
}

.form {
	display: flex;
	flex-direction: column;
	align-items: center;
	gap: 20px;
	padding: 50px;
	position: relative;
	width: 300px;
	border: 1px solid #fff2;
	border-radius: 16px;
	background: #fff2;
	text-align: center;
}

.form::before {
	content: '';
	background: linear-gradient(var(--gradientDeg), var(--col1), var(--col2) 55%);
	border-radius: inherit;
	width: 100%;
	height: 100%;
	display: block;
	position: absolute;
	z-index: -1;
	inset: 0;
	filter: blur(30px);
	animation: gradientRotate 5s linear infinite;
}

