.clearButton:not(:active):hover {
	opacity: 0.5;
}

.clearButton:focus {
	border-radius: 8px;
	border: 1px solid #fff;
	opacity: 0.5;
}

.formCaption {
	font-size: 2.5rem;
	color: #fff;
	cursor: default;
	user-select: none;
	filter: drop-shadow(0 0 10px #fff);
}

.inputContainer {
	display: flex;
	flex-direction: column;
	align-items: center;
	position: relative;
}

.submitButton {
	position: relative;
	width: 80%;
	border: none;
	border-radius: 16px;
	padding: 20px;
	background: transparent;
	font-size: 1rem;
	font-weight: 600;
	color: #fff;
	cursor: pointer;
	outline: none;
	z-index: 1;
}

.submitButton::before {
	content: '';
	position: absolute;
	inset: 0;
	border-radius: inherit;
	background: var(--col1);
	transform: skew(-30deg, 10deg);
	filter: blur(32px);
	transition: 0.2s;
	z-index: -1;
}

.submitButton:hover::before {
	transform: none;
	filter: none;
}

.submitButton:focus::before {
	transform: none;
	filter: none;
	box-shadow:
		0 0 3px 1px #fff,
		0 0 25px #fff;
}

.submitButton:active::before {
	box-shadow: 0 0 12px #0008 inset;
}

.submitButton:disabled {
	opacity: 0.5;
	filter: grayscale(1);
	pointer-events: none;
}
